import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common/index';
import { Card, Row, Col, message } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import Btn from 'components/common/button/Btn';
import FormSearchDocIn from './FormSearchDocIn';
import { getDocIns, deleteDocIn, downloadFile } from '../../services/docIn';
import { dateToString } from '../../utils/formatUtil';

class ListDocInPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  urlFormNew = '#/doc-in/new';
  urlFormEdit = '#/doc-in/edit';

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deleteDocIn(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  exportDocOut = () => {
    const { dataTable } = this.state;
    downloadFile(dataTable);
  };

  dataTable = data => {
    this.setState({
      dataTable: data,
    });
  };

  render() {
    const columns = [
      {
        title: 'Số CV đến',
        dataIndex: 'docInNo',
        key: 'docInNo',
      },
      {
        title: 'Đến ngày',
        dataIndex: 'docInDate',
        key: 'docInDate',
        render: text => dateToString(text),
      },
      {
        title: 'Nơi gửi',
        dataIndex: 'sentPlace',
        key: 'sentPlace',
      },
      {
        title: 'Số CV vào sổ',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Vào ngày',
        dataIndex: 'noteDate',
        key: 'noteDate',
        render: text => dateToString(text),
      },
      {
        title: 'Nội dung',
        dataIndex: 'docContent',
        key: 'docContent',
      },
      {
        title: 'Nơi lưu',
        dataIndex: 'savedPlace',
        key: 'savedPlace',
      },
      {
        title: 'ĐV ký nhận',
        dataIndex: 'recipientUnit',
        key: 'recipientUnit',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`${this.urlFormEdit}/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="">
        <CartHeadAction>
          <BtnAddNew href={this.urlFormNew} />
        </CartHeadAction>
        <FormSearchDocIn />
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getDocIns}
              dataTable={this.dataTable}
              columns={columns}
              rowKey="id"
            />
          </Col>
          <Col style={{ textAlign: 'center' }} span={24}>
            <Btn onClick={this.exportDocOut} icon="download">
              Download
            </Btn>
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ListDocInPage {...props} />
  </Page>
));



